html {
    box-sizing: border-box;
}
*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    min-width: 320px;
    overflow-x: hidden;
    position: relative;
}

a {
    text-decoration: none;
    color: inherit;
}

ul,
li {
    margin: 0;
    padding: 0;
    list-style: none;
}

.container {
    width: 100%;
    max-width: 1600px;
    padding: 0 24px;
    margin: 0 auto;
}

@media screen and (max-width: 1919px) {
    .container {
        max-width: 1170px;
    }
}

.active-turn {
    color: crimson;
}
.winner{
    color: #000;
}
.winning-cell {
    background-color: darkseagreen;
}
.cross, .circle{
    pointer-events: none;
}


.ttt {
    width: 100%;
    height: 100vh;
    min-height: 100vh;
    background-color: #ffe09e;
    .container {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &-wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        position: relative;
        .turn {
            padding: 15px;
            font-size: 40px;
            font-weight: 700;
            text-align: center;
        }
        .board {
            width: 302px;
            height: 302px;
            background-color: #fff;
            display: flex;
            flex-wrap: wrap;
            border: 1px solid #000;
            &-cell {
                width: 100px;
                height: 100px;
                border: 1px solid #000;
                display: flex;
                justify-content: center;
                align-items: center;
                & img {
                    width: 70%;
                    height: auto;
                    object-fit: contain;
                }
            }
        }
        .reset{
            display: none;
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translateX(-50%) translateY(150%);
            padding: 20px;
            background-color: crimson;
            border-radius: 10px;
            color: #fff;
            font-size: 18px;
            &.active{
                display: block;
            }
        }
    }
}